  /// <summary>
  /// OfficeAppContext is the class for host app to provide information for MOE to intialize.
  /// </summary>
  /// <accessibility>public</accessibility>
  /// <param name="id">The solutionId for the MOE (SharePoint app) (Type: String)</param>
  /// <param name="appName">The name of the host application (Type: OSF.AppName) </param>
  /// <param name="appVersion">The version of the host application. For example: "15"(Type: String) </param>
  /// <param name="appUILocale">UILocale of the host application. For example: "en-us"(Type: String) </param>
  /// <param name="dataLocale">Data locale of the host application document. For example: "zh-cn"(Type: String) </param>
  /// <param name="docUrl">Url of the host application document. (Type: String) </param>
  /// <param name="clientMode">The mode of the host application document. (Type: OSF.ClientMode) </param>
  /// <param name="settings">The settings of the MOE. (Type: Object) </param>
  /// <param name="reason">The reason for the MOE intialization.  (Type: Microsoft.Office.WebExtension.InitializationReason) </param>
  /// <param name="osfControlType">The Agave Type.  (Type: OSF.OsfControlType) </param>
  /// <param name="eToken">The token for current user.  (Type: String) </param>
  /// <param name="correlationId">The correlation id.  (Type: String) </param>
  /// <param name="appInstanceId">An unique Id for this App instance.  (Type: String) </param>
  /// <param name="touchEnabled">Is the host using touch input. (Type: Boolean)</param>
  /// <param name="commerceAllowed">Is the app allowed to show commerce UI at this endpoint. (Type: Boolean)</param>
  /// <param name="appMinorVersion">The minor version of the app. (Type: String)</param>
  /// <param name="requirementMatrix">JSON serialized string containing supported API. (Type: String)</param>
  /// <param name="hostCustomMessage">Allows the host to pass a custom message to office.js telemetry. (Type: String)</param>
  /// <param name="hostFullVersion">The major/minor/revision/sequence version of the host. (Type: String)</param>
  /// <param name="clientWindowHeight">The height of the host window. (Type: Integer)</param>
  /// <param name="clientWindowWidth">The width of the host window. (Type: Integer)</param>
  /// <param name="addinName">The name of current addin (Type: String)</param>
  /// <param name="appDomains">The app domain list of the agave (Type: Array)</param>
  /// <param name="dialogRequirementMatrix">JSON serialized string containing dialog requirements. (Type: String)</param>
  /// <param name="featureGates">Map of all feature gate and their values for this solution (Type: Object)"</param>
  /// <param name="officeTheme">Current OfficeThemeInfo used by the host (Type: Object)</param>
  /// <param name="initialDisplayMode">Runtime's initial display mode (Type: Integer)</param>
  export let OfficeAppContext = function OSF_OfficeAppContext(
    id,
    appName,
    appVersion,
    appUILocale,
    dataLocale,
    docUrl,
    clientMode,
    settings,
    reason,
    osfControlType,
    eToken,
    correlationId,
    appInstanceId,
    touchEnabled,
    commerceAllowed,
    appMinorVersion,
    requirementMatrix,
    hostCustomMessage,
    hostFullVersion,
    clientWindowHeight,
    clientWindowWidth,
    addinName,
    appDomains,
    dialogRequirementMatrix,
    featureGates,
    officeTheme,
    initialDisplayMode,
    hostSettings,
  ) {
    this._id = id;
    this._appName = appName;
    this._appVersion = appVersion;
    this._appUILocale = appUILocale;
    this._dataLocale = dataLocale;
    this._docUrl = docUrl;
    this._clientMode = clientMode;
    this._settings = settings;
    this._reason = reason;
    this._osfControlType = osfControlType;
    this._eToken = eToken;
    this._correlationId = correlationId;
    this._appInstanceId = appInstanceId;
    this._touchEnabled = touchEnabled;
    this._commerceAllowed = commerceAllowed;
    this._appMinorVersion = appMinorVersion;
    this._requirementMatrix = requirementMatrix;
    this._hostCustomMessage = hostCustomMessage;
    this._hostFullVersion = hostFullVersion;
    this._isDialog = false;
    this._clientWindowHeight = clientWindowHeight;
    this._clientWindowWidth = clientWindowWidth;
    this._addinName = addinName;
    this._appDomains = appDomains;
    this._dialogRequirementMatrix = dialogRequirementMatrix;
    this._featureGates = featureGates;
    this._officeTheme = officeTheme;
    this._initialDisplayMode = initialDisplayMode;
    this._hostSettings = hostSettings;
    this.get_id = function get_id() {
      return this._id;
    };
    this.get_appName = function get_appName() {
      return this._appName;
    };
    this.get_appVersion = function get_appVersion() {
      return this._appVersion;
    };
    this.get_appUILocale = function get_appUILocale() {
      return this._appUILocale;
    };
    this.get_dataLocale = function get_dataLocale() {
      return this._dataLocale;
    };
    this.get_docUrl = function get_docUrl() {
      return this._docUrl;
    };
    this.get_clientMode = function get_clientMode() {
      return this._clientMode;
    };
    this.get_bindings = function get_bindings() {
      return this._bindings;
    };
    this.get_settings = function get_settings() {
      return this._settings;
    };
    this.get_reason = function get_reason() {
      return this._reason;
    };
    this.get_osfControlType = function get_osfControlType() {
      return this._osfControlType;
    };
    this.get_eToken = function get_eToken() {
      return this._eToken;
    };
    this.get_correlationId = function get_correlationId() {
      return this._correlationId;
    };
    this.get_appInstanceId = function get_appInstanceId() {
      return this._appInstanceId;
    };
    this.get_touchEnabled = function get_touchEnabled() {
      return this._touchEnabled;
    };
    this.get_commerceAllowed = function get_commerceAllowed() {
      return this._commerceAllowed;
    };
    this.get_appMinorVersion = function get_appMinorVersion() {
      return this._appMinorVersion;
    };
    this.get_requirementMatrix = function get_requirementMatrix() {
      return this._requirementMatrix;
    };
    this.get_dialogRequirementMatrix = function get_dialogRequirementMatrix() {
      return this._dialogRequirementMatrix;
    };
    this.get_hostCustomMessage = function get_hostCustomMessage() {
      return this._hostCustomMessage;
    };
    this.get_hostFullVersion = function get_hostFullVersion() {
      return this._hostFullVersion;
    };
    this.get_isDialog = function get_isDialog() {
      return this._isDialog;
    };
    this.get_clientWindowHeight = function get_clientWindowHeight() {
      return this._clientWindowHeight;
    };
    this.get_clientWindowWidth = function get_clientWindowWidth() {
      return this._clientWindowWidth;
    };
    this.get_addinName = function get_addinName() {
      return this._addinName;
    };
    this.get_appDomains = function get_appDomains() {
      return this._appDomains;
    };
    this.get_featureGates = function get_featureGates() {
      return this._featureGates;
    };
    this.get_officeTheme = function get_officeTheme() {
      return this._officeTheme;
    };
    this.get_initialDisplayMode = function get_initialDisplayMode() {
      return this._initialDisplayMode ? this._initialDisplayMode : 0;
    };
    this.get_hostSettings = function get_hostSettings() {
      return this._hostSettings;
    };
  };
