    // The XDM requires server-side hookups for all events, but bindings and data nodes for custom xml are dynamically created and so they need specific event names for XDM registration
    export let getXdmEventName = function OSF_DDA$GetXdmEventName(id, eventType) {
      if (
        // @ts-ignore TS2339
        eventType == Microsoft.Office.WebExtension.EventType.BindingSelectionChanged ||
        // @ts-ignore TS2339
        eventType == Microsoft.Office.WebExtension.EventType.BindingDataChanged ||
        // @ts-ignore TS2339
        eventType == Microsoft.Office.WebExtension.EventType.DataNodeDeleted ||
        // @ts-ignore TS2339
        eventType == Microsoft.Office.WebExtension.EventType.DataNodeInserted ||
        // @ts-ignore TS2339
        eventType == Microsoft.Office.WebExtension.EventType.DataNodeReplaced
      ) {
        return id + "_" + eventType;
      } else {
        return eventType;
      }
    };
